@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Gilroy-Bold";
  src: url("../fonts/Gilroy-Bold.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Gilroy-Bold", sans-serif;
  font-weight: 700;
}

.visibility-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  opacity: 0 !important;
  pointer-events: none !important;
  overflow: hidden !important;
  z-index: -1;
}

ul,
ol {
  padding: 0;
  margin: 0;
  list-style: none;
}

@font-face {
  font-family: "font-icon";
  src: url("../f-icons/general-icons.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
[class*=font-icon]::before,
[class*=font-icon]::after {
  display: inline-block;
  font-family: "font-icon";
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 30px;
}

.custom-list__item a::before, .related-services__link::before, .social-list a::before {
  font-size: 1.875rem;
  display: inline-block;
  font-family: "font-icon";
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.custom-list__item a::before, .related-services__link::before {
  content: "\e900";
}

.linkedIn::before {
  content: "\e901";
}

.user::before {
  content: "\e902";
}

.tg::before {
  content: "\e903";
}

.x::before {
  content: "\e904";
}

.icon-square {
  display: inline-flex;
  align-items: center;
  padding: 0.5625rem;
  justify-content: center;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 5px;
}

