.active-menu {
  display: block;
  cursor: pointer;
  overflow: hidden;
  width: 30px;
  height: 30px;
  font-size: 0;
  text-indent: -999em;
  transition: all 0.3s ease;
  position: absolute;
  top: 30px;
  right: 20px;
  z-index: 222;
}
.active-menu:focus {
  outline: none;
}
.active-menu span {
  display: block;
  position: absolute;
  top: 14px;
  left: 0;
  right: 0;
  height: 3px;
  background: rgba(255, 255, 255, 0.6);
  transition: background-color 0.3s ease;
}
.active-menu span::before, .active-menu span::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: rgba(255, 255, 255, 0.6);
  transition-duration: 0.3s, 0.3s;
}
.active-menu span::before {
  top: -7px;
  transition-property: top, transform;
}
.active-menu span::after {
  bottom: -7px;
  transition-property: bottom, transform;
}
.display-menu .active-menu {
  position: fixed;
}
.display-menu .active-menu span {
  background-color: transparent;
}
.display-menu .active-menu span::before, .display-menu .active-menu span::after {
  background-color: #fff;
}
.display-menu .active-menu span::before {
  top: 0;
  transform: rotate(45deg);
}
.display-menu .active-menu span::after {
  bottom: 0;
  transform: rotate(-45deg);
}

