.anchor__trigger {
  opacity: 0.6;
  padding: 14px 20px 10px 20px;
  border: 1px solid transparent;
  border-radius: 99rem;
  display: inline-flex;
  transition: color 0.3s ease, border-color 0.3s, opacity 0.3s ease;
