}

.breadcrumbs {
  font-size: 0.75rem;
  margin: 40px 0;
}
.breadcrumbs__list {
  display: flex;
  align-items: center;
}
.breadcrumbs a {
  color: rgba(255, 255, 255, 0.4);
}
.breadcrumbs a:hover {
  color: #fff;
}
.breadcrumbs li + li::before {
  content: "/";
  color: rgba(255, 255, 255, 0.2);
  margin: 0 2px 0 4px;
}

.page-section {
  color: rgba(255, 255, 255, 0.6);
}

.block-statistic {
  display: flex;
  padding: 21px 40px;
  background: linear-gradient(to right, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.01));
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.statistic-item .number {
  font-size: 2.125rem;
  color: transparent;
  font-family: "Gilroy-Bold", sans-serif;
  margin-bottom: 10px;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  background-clip: text;
}
.statistic-item .label {
  font-size: 0.875rem;
  color: #fff;
}
.statistic-item + .statistic-item {
  position: relative;
  margin-left: 60px;
}
.statistic-item + .statistic-item::before {
  content: "";
  width: 1px;
  height: 100%;
  display: block;
  position: absolute;
  left: -30px;
  background: linear-gradient(118.29deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0.2) 100%);
}

.intro {
  padding-top: 200px;
  display: flex;
  justify-content: center;
  gap: 80px;
}
.intro h1 {
  margin-bottom: 2.5rem;
}
.intro p {
  font-size: 1.125rem;
  line-height: 1.6;
}
.intro .block-info {
  max-width: 480px;
}

.basic-page-header {
  position: relative;
  padding: 60px 0;
}
.basic-page-header h1 {
  margin: 0;
}
.basic-page-header::before {
  content: "";
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  background: linear-gradient(326.32deg, #273dd2 0%, #6bb9ec 77.5%, #7ac4f4 100%);
  width: 100vw;
  z-index: -1;
}

.page-content {
  margin: 60px 0;
  max-width: 720px;
}
.page-content p {
  font-size: 1.125rem;
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

.content-block {
  padding: 0 40px;
  color: rgba(255, 255, 255, 0.6);
}
.content-block h2 {
  font-size: 1.125rem;
  line-height: 1.6;
  margin-bottom: 0.625rem;
}

.page-not-found__container {
  max-width: 680px;
  margin: 0 auto;
}
.page-not-found__image {
  margin-bottom: 60px;
}
.page-not-found__content {
  font-size: 1.125rem;
  text-align: center;
  color: #fff;
}
.page-not-found__content p {
  margin-bottom: 50px;
}
.page-not-found__content .btn-link {
  font-family: "Gilroy-Bold", sans-serif;
}